.app-wrapper{
  display: grid;
  grid-template-areas: 'header header'
'nav content ';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 1fr 5fr;
  grid-gap: 10px;
  width: 1200px;
  margin: 0 auto;
}
.app-wrapper-content{
  grid-template-areas: 'content';
  background-color: #5aa488;
  font-size: 30px;
}
